@import "../../../../sass/abstracts/mixins";
@import "../../../../sass/abstracts/constants";
@import '../../../../sass/abstracts/placeholders';

.button {
  width: 167px;
  padding: 8px 26px;
  @include font(1.8rem, 2.4rem);
  border-radius: 4px;
  @extend %transition;

  &_bordered {
    background-color: transparent;
    border: 1px solid $color-primary;
    color: $color-primary;
    cursor:default;
  }

  &_colored {
    background-color: $color-primary;
    color: $color-light;
    border: 1px solid $color-primary;
    &:hover {
      background: $color-hovered-primary;
      border-color: $color-hovered-primary;
    }
  }
}
