@import "../../../../sass/abstracts/constants";
@import "../../../../sass/abstracts/mixins";
@import "../../../../sass/abstracts/placeholders";

.contact-us {
  padding-top: 100px;
  margin-top: -100px;
  background: rgba(161, 194, 209, 0.5);
  -webkit-background-clip: content-box;
  background-clip: content-box;
  display: flex;
}
.contact-us__wrapper {
  text-align: left;
  padding: 0 100px 0 140px;
  flex-grow: 1;
  justify-content: start;
}

.contact-us__content {
  text-align: left;
}
.contact-us__title {
  max-width: 570px;
  padding-left: 0;
  padding-top: 45px;
}
.contact-us__form {
  input,
  textarea {
    &:focus {
      outline: none;
    }
  }
}

.contact-us__input {
  display: block;
  width: 48%;
}

textarea {
  resize: none;
  width: 100%;
  height: 140px;
}

.contact-us__line {
  position: relative;
  margin-bottom: 19px;
  justify-content: space-between;
  width: 100%;
  gap: 10px;
}

.contact-us__image {
  text-align: right;
  & > img{
    width: 100%;
    object-fit: cover;
    vertical-align:bottom ;
  }
}
